
<template >
  <v-container class="grey darken-4 px-8 pt-6 pb-10" :style="{maxWidth:this.$store.state.contentMaxWidth}" >
    <v-row class="subtitle-1">
		  보도자료  	   
    </v-row>

    <v-row class="mt-4 mb-6">
      <v-divider class="pink darken-1 "></v-divider>
    </v-row>


		<v-row>
		  <v-col cols="12" sm="6" v-for="info,i in infos" :key="i"  > 
				<v-card class="mx-auto" max-width="400">
					<v-img :src="info.imgUrl" :lazy-src="info.imgUrl" height="200px" class="grey darken-4 ">
						<template v-slot:placeholder>
							<v-row class="fill-height ma-0" align="center" justify="center">
								<v-progress-circular indeterminate color="grey darken-2"></v-progress-circular>
							</v-row>
						</template>
					</v-img>

					<v-card-text class="text--primary">
						<div>{{info.title}}</div>
						<div>{{info.date}}</div>
					</v-card-text>

					<v-card-actions>
						<v-spacer></v-spacer>
						<v-btn color="pink lighten-1" text @click="clickCard(i)">자세히</v-btn>
					</v-card-actions>
				</v-card>
      </v-col>

    </v-row>


   <div class="text-center">
    <v-dialog v-model="dialog" width="900" >
      <v-card>
        <v-card-title class="subtitle-1 black" primary-title>
          {{ dialogData.title}}
        </v-card-title>

        <v-card-text class="black">
          <iframe id="broad-video" style="width:100%;" :height="iframHeight" :src="dialogData.url" frameborder="0" 
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        </v-card-text>
        <v-divider></v-divider>

        <v-card-actions class="black">
          <v-spacer></v-spacer>
          <v-btn color="white" text @click="closeDialog()">닫기</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
   </div>

  </v-container>
</template>

<script>
  export default {
    data: () => ({
      dialog: false,
      dialogData: { title: '', url: ''},
      url:"https://i.imgur.com/uleWbB9.png",
      infos : [
        { imgUrl : "https://i.imgur.com/lL9b3dL.png", videoUrl:"https://www.youtube.com/embed/1YTPYZqlt3g"  , 
        title: "춘천 KBS 금병산 예술제 뉴스보도", date: "2015.09.12" },

        { imgUrl : "https://i.imgur.com/8ezjaRf.png", videoUrl: "https://www.youtube.com/embed/G0jazJcnTVc" , 
        title: "MBC 강원365 소화갤러리 방영", date: "2014년 10월 중 방영" },
      ],
      iframHeight: 450,
    }),
  
    mounted(){
     if(window.innerWidth < 700)
       this.iframHeight =  window.innerHeight/2;
    },

    methods : {
      clickCard(idx) {
        this.dialogData.title = this.infos[idx].title;
        this.dialogData.url = this.infos[idx].videoUrl;
        this.dialog = true;
      },
      closeDialog(){
        const iframe = document.getElementById('broad-video');
        iframe.src = iframe.src;

        this.dialog = false; 
      },
    },
  }

</script>
