import Vue					from "vue";
import Router				from "vue-router";
import Home					from "../views/Home.vue";
import Intro				from "../views/gallery/Intro.vue";
import Author				from "../views/gallery/Author.vue";
import Directions		from "../views/gallery/Directions.vue";

import Tradition		from "../views/artwork/Tradition.vue";
import Application  from "../views/artwork/Application.vue";
import Craft				from "../views/artwork/Craft.vue";
import Government		from "../views/artwork/Government.vue";

import SightSeeing	from "../views/info/SightSeeing.vue";
import Exhibition	  from "../views/info/Exhibition.vue";
import Landscape	  from "../views/info/Landscape.vue";
import Broad	  	  from "../views/info/Broad.vue";

import ClassNotice	from "../views/lesson/ClassNotice.vue";
import ClassPhoto	  from "../views/lesson/ClassPhoto.vue";


Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }
  },
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/intro",
      name: "intro",
      component: Intro 
    },
    {
      path: "/author",
      name: "author",
      component: Author 
    },
    {
      path: "/directions",
      name: "directions",
      component: Directions 
    },


    {
      path: "/traditional-folk-painting",
      name: "traditional-folk-painting", 
      component: Tradition 
    },
    {
      path: "/application-folk-painting",
      name: "application-folk-painting",
      component: Application
    },
    {
      path: "/craft-folk-painting",
      name: "craft-folk-painting",
      component: Craft
    },
    {
      path: "/government-folk-painting",
      name: "government-folk-painting",
      component: Government
    },


    {
      path: "/class-notice",
      name: "class-notice",
      component: ClassNotice
    },
    {
      path: "/class-photo",
      name: "class-photo",
      component: ClassPhoto
    },


    {
      path: "/sight-seeing",
      name: "sight-seeing",
      component: SightSeeing
    },
    {
      path: "/exhibition-notice",
      name: "exhibition-notice",
      component: Exhibition
    },
    {
      path: "/landscape",
      name: "landscape",
      component: Landscape
    },
    {
      path: "/broad",
      name: "broad",
      component: Broad
    },

  ]
});
